<template>
  <div id="contact_page" class="page">
    <div v-if="global.mode === 0">
      <div class="title_part">
        <div :class="'title_'+global.lang">{{ global.lang_pack.contact }}</div>
      </div>

      <div class="contact_part">
        <div class="big_text_part">
          <div class="big_text">{{ global.lang_pack.let_connect }}</div>
          <div class="connect_email">{{ global.lang_pack.connect_email }}</div>
        </div>

        <div  class="right_part">
          <div  class="right_title">{{ global.lang_pack.hongkong }}</div>
          <div  :class="'content_text_'+global.lang">{{ global.lang_pack.hongkong_address }}</div>
          <div  class="right_title shenzhen_title">{{ global.lang_pack.shenzhen }}</div>
          <div  :class="'content_text_'+global.lang">{{ global.lang_pack.shenzhen_address }}</div>
        </div>
      </div>

      <div class="contact_part" style="margin-top: 160px">
        <div class="big_text_part">
          <div class="big_text">{{ global.lang_pack.we_are_hiring }}</div>
        </div>

        <div class="right_part">
          <div  :class="'content_text_'+global.lang" style="margin-bottom: 120px">{{ global.lang_pack.hire_intro }}</div>
          <a class="apply_title" href="mailto:info@signcheckhk.com">{{ global.lang_pack.apply }}</a>
          <div  class="apply_line"></div>
        </div>
      </div>
    </div>

    <!--手机版本的css样式-->
    <div v-if="global.mode === 1" class="mobile_body">
      <div class="title_part">
        <div :class="'title_'+global.lang">{{ global.lang_pack.contact }}</div>
      </div>

        <div class="big_text_part">
          <div class="big_text">{{ global.lang_pack.let_connect }}</div>
          <div class="connect_email">{{ global.lang_pack.connect_email }}</div>
        </div>

      <div class="grey_line" style="margin-top: 60px; margin-bottom: 60px"></div>

        <div  class="right_part">
          <div  class="right_title">{{ global.lang_pack.hongkong }}</div>
          <div  :class="'content_text_'+global.lang">{{ global.lang_pack.hongkong_address }}</div>
          <div  class="right_title shenzhen_title">{{ global.lang_pack.shenzhen }}</div>
          <div  :class="'content_text_'+global.lang">{{ global.lang_pack.shenzhen_address }}</div>
        </div>
      <div class="grey_line" style="margin-top: 60px; margin-bottom: 60px"></div>

        <div class="big_text_part" style="margin-bottom: 10px">
          <div class="big_text">{{ global.lang_pack.we_are_hiring }}</div>
        </div>

        <div class="right_part">
          <div  :class="'content_text_'+global.lang" style="margin-bottom: 50px">{{ global.lang_pack.hire_intro }}</div>
          <a class="apply_title" href="mailto:info@signcheckhk.com">{{ global.lang_pack.apply }}</a>
          <div  class="apply_line"></div>
        </div>
      <div class="grey_line" style="margin-top: 60px; margin-bottom: 60px"></div>


    </div>

    <Footer></Footer>
    <LangTransfer></LangTransfer>
    <Menu></Menu>
  </div>
</template>

<style lang="css" scoped>
div.page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}

div.contact_part {
  margin-top: 100px;
  display: flex;
  flex-direction: row;
  padding-left: 11.98%;
  width: 100%;
  align-items: center;
}

div.big_text_part {
  width: 973.33rem;
  display: flex;
  flex-direction: column;
}

div.big_text {
  font-family: blk_en;
  font-size: 70px;
  letter-spacing: 5px;
  line-height: 80px;
  text-transform: capitalize;
  white-space: pre-line;
}

div.connect_email {
  padding-top: 30px;
  font-size: 45px;
  letter-spacing: 1px;
}

div.right_part {
  width: 486.667rem;
}

div.right_title {
  font-size: 40px;
  margin-bottom: 20px;
}

div.shenzhen_title {
  margin-top: 120px;
}

a.apply_title {
  font-family: blk_en;
  color: #37383d;
  font-size: 50px;
  text-decoration: none;
  letter-spacing: 5px;
  border-bottom: solid 5px #efcd24;
}

/*手机版样式*/
@media screen and (max-width: 540px) {
  div.contact_part {
    margin-top: 110px;
    display: flex;
    flex-direction: row;
    padding-left: 11.98%;
    width: 100%;
    align-items: center;
  }

  div.big_text_part {
    width: unset;
    display: flex;
    flex-direction: column;
  }

  div.big_text {
    font-family: blk_en;
    font-size: 20px;
    letter-spacing: unset;
    line-height: unset;
    text-transform: capitalize;
    white-space: pre-line;
  }

  div.connect_email {
    padding-top: 10px;
    font-size: 15px;
    letter-spacing: 1px;
  }

  div.right_part {
    width: unset;
  }

  div.right_title {
    font-size: 20px;
    margin-bottom: unset;
  }

  div.shenzhen_title {
    margin-top: 30px;
  }

  a.apply_title {
    font-family: blk_en;
    color: #37383d;
    font-size: 20px;
    text-decoration: none;
    letter-spacing: 1px;
    border-bottom: solid 5px #efcd24;
  }

  div.grey_line{
    position: relative;
    height: 0.5px;
    transform-origin: bottom;
    background: #37383d;
  }
}

</style>

<script>
import global from "../global";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"

//export default
export default {
  name: "Contact",
  data() {
    return {
      global: global,
    };
  },
  components: {
    Menu,
    LangTransfer,
    Footer
  },
  beforeCreate() {
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  destroyed() {
  },
  watch: {
  },
  methods: {
  },
};
</script>



